	getConsigneeDetailsList = () => {

		let apiUrlConsigneeDetails = `http://localhost:8080/srd/master/v1/consigneedetails`;
		this.http.get(apiUrlConsigneeDetails, {
			params: {
				masterDto: this.getUserValuesForConsigneeList(),
				city: 'Chennai',
			},
		}).subscribe(
			(response) => {
				if (response.json().length == 0) {
					alert('No records found for the selected city!');
				} else {
					this.consigneeDetailsDataList = response.json();
					this.dtTriggerConsigneeDetails.next();
					//console.log(this.consigneeDetailsDataList[5]);
				}
			}),
			(error) => console.log(error.json()),
			() => console.log('done');

	};
